import {GlobalTable} from '@/modules/contract/house/globalTable';
import {Asset} from '@/modules/eos/asset';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';
import {Constants} from '@/modules/eos/constants';
import {Network} from '@/modules/eos/network';
import {BaccaratBetType, BaccaratGameTable} from '@/modules/contract/baccarat/baccaratGameTable';
import {BaccaratBetTable} from '@/modules/contract/baccarat/baccaratBetTable';
import {GameContract} from '@/modules/contract/house/gameContract';
import {HouseContract} from '@/modules/contract/house/houseContract';


export class BaccaratContract extends GameContract {
    public static readonly CONTRACT = Constants.NETWORK === Network.LOCALHOST ? 'baccarat' : 'godappbaccar';
    public static readonly ID: number = 3;

    public readonly globalVarTable: GlobalTable;
    public readonly gameTable: BaccaratGameTable;
    public readonly betTable: BaccaratBetTable;


    public constructor(house: HouseContract) {
        super(BaccaratContract.CONTRACT, house, BaccaratContract.ID);

        this.globalVarTable = new GlobalTable(this.eos, this.name);
        this.gameTable = new BaccaratGameTable(this.eos, this.name);
        this.betTable = new BaccaratBetTable(this.eos, this.name);
    }

    public init(): Promise<TransactionReceipt> {
        return this.pushAction('init', {});
    }

    public setGlobal(key: number, value: number): Promise<TransactionReceipt> {
        return this.pushAction('setglobal', {key, value});
    }

    public close(id: number): Promise<TransactionReceipt> {
        return this.pushAction('hardclose', {game_id: id});
    }

    public play(gameId: number, betType: BaccaratBetType, asset: Asset): Promise<TransactionReceipt> {
        return this.house.transfer(asset, this.id + ',' + gameId + ',' + betType + ',');
    }
}
